.sidebar {
  position: fixed;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  padding: 55px 40px;
  top: 20px;
  left: -120%;
  bottom: 20px;
  overflow: auto;
  overflow-x: hidden;
  min-width: 350px;
  max-width: 350px;
  z-index: 5;
  transition: left .3s ease-out;
  &.active {
    left: 0;
    .sidebar__close-btn {
      display: block; } }
  @include breakpoint(lg) {
    overflow: unset;
    padding: 0;
    background: none;
    position: static;
    min-width: auto; }
  &__section {
    margin: 0 0 50px 0;
    &:last-child {
      margin: 0; }
    #price-from,#price-to {
      max-width: 70px;
      margin: 0 30px 0 0; }
    #power {
      color: #D11586;
      font-weight: 500; } }
  &__title {
    color: #0066BA;
    font-weight: 700;
    font-size: 22px;
    line-height: 26px;
    margin: 0 0 25px; }

  &__btn {
    display: inline-block;
    + .input-field {
      float: right; } }

  &__close-btn {
    display: none;
    position: absolute;
    top: 10px;
    right: 10px;
    //transform: translateX(calc(100% + 15px))
    cursor: pointer;
    z-index: 4; } }













#sidebar-overlay {
  position: fixed;
  display: none;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(63, 63, 63, 0.56);
  z-index: 4;
  &.active {
    display: block; } }
